.container {
  border-radius: 14px;
  border-width: 2px;
  border: .3px solid rgb(211, 211, 211);
  box-shadow: rgba(0, 0, 0, 0.6) 0px 2px 8px;
  padding-top: 15px;
  padding-bottom: 15px;
  margin-top: 36px;
  margin-bottom: 50px;

  position: relative;
}

.titleWrapper{
  font-weight: 700;
  font-size: 28px;

  display: flex;
  justify-content: center;
  align-items: center;
}

.btnWrapper {
  position: absolute;
  padding: 6px;
  border-radius: 12px;
  top: 44px;
  right: 28px;
  display: flex;
  align-items: center;
  justify-content: center;
  
  border: .3px solid rgb(211, 211, 211);
  cursor: pointer;

  transition: .2s;
}

.btnWrapper:hover {
  background-color: red;
  border: .3px solid rgb(255, 0, 0);
  box-shadow: rgba(255, 0, 0, 0.6) 0px 2px 8px;
}

.deleteBtnIcon {
  width: 30px;
  height: 30px;
}

.descriptionWrapper {
  font-size: 20px;
}

.titleWrapper,
.descriptionWrapper {
  border-bottom: .3px solid rgb(211, 211, 211);
  border-top: .3px solid rgb(211, 211, 211);
}

.imgContainer {
  display: flex;
  align-items: center;
  justify-content: center;
}

.imgStyle {
  max-width: 1200px;
  height: 600px;
}

.authorWrapper {
  border-bottom: .3px solid rgb(211, 211, 211);

  height: 50px;
  font-size: 18px;
  font-weight: 700;
  display: flex;
  justify-content: right;
  align-items: center;
  padding-right: 34px;
}