.contentContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 14;
  width: 250px;
  height: 400px;
  position: fixed;
  border: solid black 14px;
}

.marker {
  position: absolute;
  border-radius: 50%;
  background: greenyellow;
  width: 14px;
  height: 14px;
  box-shadow: rgba(0, 0, 0, 0.6) 0px 2px 8px;
  transform: translate(-50%, -50%);
}

