.titleContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 210px;
}

.container {
  position: relative;
  width: fit-content;
}

.img {
  width: 100%;
  height: auto;
}

.textOverlay {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  text-shadow:
    -2px -2px 0 #000,  
    2px -2px 0 #000,
    -2px 2px 0 #000,
    2px 2px 0 #000;
  font-size: 32px;
  text-transform: uppercase;
  font-weight: bold;
}

.cardContainer{
  display: flex;
  justify-content: center;
}

.card {
  width: 300px;
  height: 190px;
  font-weight: bold;
  margin-left: 18px;
  margin-right: 18px;
  font-size: 28px;
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  border: none;
  border-radius: 24px;
  box-shadow: rgba(0, 0, 0, 2.6) 0px 2px 8px;
  transition: .2s;
}

.card:hover {
  letter-spacing: 2px;
  box-shadow: rgba(0, 0, 0, 4.6) 0px 10px 20px;
}