.formWrapper {
  margin-top: 14px;
  padding-top: 40px;
  padding-bottom: 40px;
  border-radius: 26px;
  border: .3px solid rgb(211, 211, 211);
  box-shadow: rgba(0, 0, 0, 0.6) 0px 2px 8px;

  height: 600px;
  width: 440px;
}

.divElement {
  margin-top: 8px;
  margin-bottom: 8px;
  border-top: .3px solid rgb(211, 211, 211);
  border-bottom: .3px solid rgb(211, 211, 211);
  padding-left: 40px;
  padding-right: 40px;
  display: flex;
  flex-direction: column;
}

.label {
  padding-left: 12px;
  font-weight: 700;
  font-size: 20px;
  margin-bottom: 8px;
}

.textField {
  outline: none;
  border: .3px solid rgb(145, 144, 144);
  font-size: 20px;
  padding: 4px;
  padding-left: 12px;
  
  border-radius: 12px;
  margin-bottom: 12px;
}

.fileInput {
  margin-bottom: 12px;
}

.dropdown {
  outline: none;
  border: .3px solid rgb(145, 144, 144);
  border-radius: 14px;
  padding: 4px;
  padding-left: 14px;
  margin-bottom: 12px;
  font-size: 20px;
  background-image: none;
}

.btnWrapper {
  display: flex;
  align-items: center;
  justify-content: center;
}

.btn {
  margin-top: 16px;
  padding-top: 14px;
  padding-bottom: 14px;
  padding-left: 50px;
  padding-right: 50px;
  font-size: 24px;
  font-weight: 700;
  text-transform: uppercase;
  border: .3px solid rgb(145, 144, 144);
  border-radius: 32px;
  background-color: white;
  cursor: pointer;
  box-shadow: rgba(0, 0, 0, 0.6) 0px 2px 8px;
  transition: .2s;
}

.btn:hover {
  background-color: rgb(76, 197, 0);
  padding-left: 70px;
  padding-right: 70px;
}

.mapWrapper {
  margin-top: 40px;
}