.header {
  display: flex;
  justify-content: space-between;

  top: 0;
  left: 0;
  right: 0;

  box-shadow: rgba(0, 0, 0, 0.6) 0px 2px 8px;

  padding-top: 18px;
  padding-bottom: 18px;
  height: 50px;
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.btn {
  color: black;
  font-weight: 700;
  font-size: 18px;
  text-decoration: none;
  padding: 10px;
  border-radius: 6px;
  box-shadow: rgba(0, 0, 0, 0.6) 0px 2px 8px;
  margin-left: 8px;
  margin-right: 8px;
  cursor: pointer;
  transition: .2s;
}

.btn:hover {
  background-color: rgba(0, 0, 0, 0.12);
}

.logoContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 130px;
  height: 50px;
}

.logo {
  width: 150px;
}

.cogContainer {
  display: flex;
  justify-content: center;
  align-items: center;

  position: relative;

  padding: 8px;
  margin-right: 24px;

  height: 38px;
  width: 38px;
  border-radius: 36px;

  box-shadow: rgba(0, 0, 0, 0.6) 0px 2px 8px;
  cursor: pointer;

  transition: .2s;
}

.cogContainer:hover {
  background-color: rgba(0, 0, 0, 0.12);
}

.dropdownContainer {
  background-color: white;
  padding-top: 6px;
  padding-bottom: 6px;
  border-radius: 4px;
  box-shadow: rgba(0, 0, 0, 0.6) 0px 2px 8px;
  position: absolute;
  top: 50px;
  right: 5px;
}

.dropdown {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.cogIcon {
  width: 38px;
}

.uploadBtn {
  display: flex;
  justify-content: center;
  align-items: center;

  color: black;
  font-weight: 700;
  font-size: 18px;
  text-decoration: none;
  padding: 10px;
  border-radius: 6px;
  box-shadow: rgba(0, 0, 0, 0.6) 0px 2px 8px;
  margin-left: 32px;
  margin-right: 8px;
  cursor: pointer;
  transition: .2s;
}

.uploadBtn:hover {
  background-color: rgba(0, 0, 0, 0.12);
}

.uploadIcon {
  width: 24px;
}