.mapBtn {
  position: fixed;
  bottom: 0;
  right: 0;

  display: flex;
  align-items: center;
  justify-content: center;

  font-weight: 600;
  font-size: 22px;

  margin-right: 24px;
  margin-bottom: 24px;

  width: 80px;
  height: 80px;

  border-radius: 50px;

  box-shadow: rgba(0, 0, 0, 0.6) 0px 2px 8px;

  cursor: pointer;
  z-index: 12;

  overflow: hidden;
  transition: .2s;
}

.mapBtn:hover {
  background-color: rgba(0, 0, 0, 0.24);
}

.clickableArea {
  width: 100%;
  height: 100%;

  display: flex;
  align-items: center;
  justify-content: center;
}

